import React, { useState, useEffect } from "react"
import Poster from "../components/Poster"
import Input from "../components/Input"
import Jump from "../components/Jump"
import SyncLoader from "react-spinners/SyncLoader"

import axios from "axios"
import jikan from "jikanjs"
import { Link } from "react-router-dom"
import { LoggedContext } from "../components/LoggedContext"


const isDuplicate = (data, obj) =>
	data.some(el =>
		Object.entries(obj).every(([key, value]) => value === el[key])
	)

const Library = () => {
	var [posters, setPosters] = useState([])
	const [loading, setLoading] = useState(false)
	const [existed, setExisted] = useState("")
	const [search, setSearch] = useState("")
	const { loggedIn, setLoggedIn } = React.useContext(LoggedContext)


	const addPoster = title => {
		jikan.search("anime", title).then(res => {
			const anime = res.results[0]

			const name = anime.title
			const href = anime.url
			const img = anime.image_url
			const data = { name, img, href }

			if (!isDuplicate(posters, data)) {
				setPosters([data, ...posters])
				setExisted("")

				axios.post("https://mysql-animes.herokuapp.com/add", data)
			} else setExisted("Existed in the list!")
		})
	}


	const deletePoster = name => {
		setPosters(posters.filter((poster) => poster.name !== name))
		setExisted("")
		axios.delete(`https://mysql-animes.herokuapp.com/remove/${name}`)
	}


	const getPosters = () => {
		setLoading(true)
		axios.get(`https://mysql-animes.herokuapp.com/animes`).then((res) => {
			setPosters(res.data.reverse())
			setLoading(false)
		})
	}


	useEffect(() => {
		const sessionLogged = sessionStorage.getItem("loggedIn")
		if (sessionLogged) {
			setLoggedIn(sessionLogged)
		}
		getPosters()
	}, [setLoggedIn])


	posters = posters.filter(poster => {
		if (search === "") return true
		else if (poster.name.toLowerCase().includes(search.toLowerCase())) return true
		else return false
	})

	return (
		<div className="panel">
			<Jump />
			<div className="banner">
				<img
					src={"https://vistapointe.net/images/little-witch-academia-1.jpg"}
					alt="banner-src"
				/>
			</div>
			<div className="input">
				<input
					type="text"
					value={search}
					onChange={e => {
						setSearch(e.target.value)
						window.scrollTo(0, 600)
					}}
					placeholder="Search something. . ." />

				{loggedIn ? (
					<Input callback={addPoster} placeholder="Add anime . . ." />
				)
					: <Link className="login" to="/login" >Authen to revise the library </Link>}

				<p>{posters.length} animes in this library..</p>
				<p>{existed}</p>
				<button onClick={getPosters} className="refresh">Refresh</button>
			</div>

			<div className="anime-wrapper">
				{loading ? (
					<div className="loader">
						<SyncLoader
							color={"#3895d3"}
							loading={loading}
							size={10}
							margin={20} />
					</div>
				) : (
					<>
						{posters.map((poster, i) => (
							<Poster
								name={poster.name}
								href={poster.href}
								img={poster.img}
								key={i}
								deletePoster={deletePoster} />
						))}
					</>
				)}
			</div>
		</div>
	)
}

export default Library
