import React, { useState } from 'react'

function Jump() {
  const [visible, setVisible] = useState(false);
  const [opacity, setOpacity] = useState(0);

  const backToTop = () => {
    window.scrollTo(0, 400);
  };

  const toggleVisible = () => {
    const scrolled = document.documentElement.scrollTop;
    if (scrolled > 600) {
      setVisible(true);
      setOpacity(1)
    } else if (scrolled <= 600) {
      setVisible(false);
    }
  };

  window.addEventListener("scroll", toggleVisible);

  return (
    <div className="jump center" style={{ display: visible ? "flex" : "none", opacity: { opacity } }}>
      <img
        src="http://cdn.onlinewebfonts.com/svg/img_248181.png"
        onClick={backToTop}
        alt="arrow"
      />
    </div>
  )
}

export default Jump
